<template>
    <nav class="topbar">
        <div class="container">
            <div class="topbar--icon">
                <a href="https://github.com/clipwww" title="clipwww github" target="_black">
                    <i class="ion-social-github"></i>
                </a>
            </div>
            <div class="topbar--search">
                <div class="topbar--search--wrap">
                    <input type="text" />
                    <button><i class="ion-android-search"></i></button>
                </div>
            </div>
            <div class="topbar--right">
                <div class="topbar--right--my">
                    <figure>
                        <img src="https://scontent-tpe1-1.xx.fbcdn.net/v/t1.0-1/10308366_744288482259570_8989851254586754317_n.jpg?oh=d65c95fd544a2d620c4375fcf190bf2c&oe=59BF0B0E" />
                        <figcaption>簡爾廷</figcaption>
                    </figure>
                </div>
                <div class="topbar--right--link">
                    <router-link to="/" class="topbar--right--home">
                        首頁
                    </router-link>
                </div>
            </div>
        </div>
    </nav>
</template>

<script>
    export default {

    
    }
</script>

<style lang="scss">
    $topbarHeight: 50px; 
    .topbar {   
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        background-color: #3b5998;
        height: $topbarHeight;
        overflow: hidden;

        &--icon, &--search, &--right{
            display: inline-block;
            height: $topbarHeight;
            color: white;
        }

        &--icon {
            position: relative;

            a {
                display: block;
                position: relative;
                width: 40px;
                height: 100%;
                color: white;
                text-decoration: none;
    
                i {
                    position: absolute;
                    top: 50%;
                    left: 0;
                    transform: translateY(-50%);
                    font-size: 40px;
                }

                span{
                    padding-left: 50px;
                }
    
            }
        } //end icon

        &--search{
            position: relative;
            width: 250px;
            &--wrap{
                position: absolute;
                top: 50%;
                left: 0;
                transform: translateY(-50%);
                height: 25px;
                width: 100%;
                background-color: white;
                border-radius: 5px;
                padding: 5px 10px 0;

                input{
                    display: inline-block;
                    border: none;
                    width: 80%;

                    &:focus{
                        outline: none;
                        box-shadow: none;
                        outline-offset: 0;
                        border: none;
                    }
                }

                button{
                    position: absolute;
                    right: 0;
                    top: 0;
                    bottom: 0;
                    width: 20%;
                    background-color: #f6f7f9;
                    border: none;
                    border-radius: 0 5px 5px 0;
                    height: 100%;
                    cursor: pointer;
                    &:focus, &:active{
                        outline: none;
                        box-shadow: inset 1px 1px 5px rgba(0,0,0,.3);
                        outline-offset: 0;
                        border: none;
                    }
                    i{
                        font-size: 20px;
                        &::before{
                            position: absolute;
                            top: 50%;
                            left: 50%;
                            transform: translate(-50%, -50%);
                        }
                    }
                }
            }

        }//end search
        
        &--right{
            float: right;
            &--my{
                position: relative;
                display: inline-block;

                figure{
                    display: table;
                    padding: 0 10px 0 0;
                    margin: 0;
                    margin-top: 10px;
                    cursor: pointer;
                    border-radius: 5px;

                    &:hover{
                        background-color: rgba(0,0,0,.1);
                    }

                    img{
                        display: table-cell;
                        width: 30px;
                        margin-right: 10px;
                        border-radius: 5px;
                    }
                    figcaption{
                        display: table-cell;
                        vertical-align: middle;
                        font-size: 18px;
                    }
                }
            }//end my

            &--link{
                display: inline-block;
                a{
                    display: inline-block;
                    position: relative;
                    color: white;
                    text-decoration: none;
                    font-size: 17px;
                    padding: 5px;
                    transform: translateY(-10px);
                    &:hover{
                        background-color: rgba(0,0,0,.1);
                    }

                    &::before{
                        content: "";
                        position: absolute;
                        left: -5px;
                        top: 50%;
                        transform: translateY(-50%);
                        width: 2px;
                        height: 80%;
                        background-color: rgba(0,0,0, .1);
                    }
                }
            
            }//end right
        }
    }
</style>



